























import { Component, Vue, Prop } from 'vue-property-decorator';
import SectionLanding from '@/components/SectionLanding.vue';
import SectionPortfolio from '@/components/SectionPortfolio.vue';
import SectionContact from '@/components/SectionContact.vue';
import SectionAbout from '@/components/SectionAbout.vue';
import NavBar from '@/components/NavBar.vue';
import { NavItem } from '@/models/navItem';
import SlideIn from '@/components/Animations/SlideIn.vue';

@Component({
  components: {
    SectionLanding,
    NavBar,
    SlideIn,
    SectionPortfolio,
    SectionContact,
    SectionAbout
  }
})
export default class HomePage extends Vue {
  navItems: NavItem[] = [];

  // Necessary to prevent the first section to jump because navbar got fixed
  fixed = false;

  mounted() {
    this.navItems = [
      {
        displayName: this.$t('navigation.home') as string,
        sectionName: 'section-landing',
        active: true
      },
      {
        displayName: this.$t('navigation.about') as string,
        sectionName: 'section-about',
        active: false
      },
      {
        displayName: this.$t('navigation.portfolio') as string,
        sectionName: 'section-portfolio',
        active: false
      },
      {
        displayName: this.$t('navigation.contact') as string,
        sectionName: 'section-contact',
        active: false
      }
    ];
  }

  selectNavItem(navItem: NavItem) {
    this.navItems = this.navItems.map(item =>
      item === navItem ? { ...item, active: true } : { ...item, active: false }
    );
  }
}
